.main {
  padding: 0 3.6rem;
}

.breadcrumb {
  margin: 1.2rem 0 3.6rem;
}

.notFound {
  height: 50vh;
  text-align: center;
  font-size: 1.6rem;
  font-family: "roboto";
}

@media (min-width: 600px) {
  .main {
    padding: 0 4.8rem;
  }
}

@media (min-width: 900px) {
  .main {
    padding: 0 7.2rem;
  }
}

@media (min-width: 1200px) {
  .main {
    padding: 0 9.6rem;
  }
}
